import React, { Component } from 'react';

import {
  CButton,
  CCard,
  CCardBody,
  CCardFooter,
  CCardHeader,
  CCol,
  CForm,
  CFormGroup,
  CFormText,
  CInput,
  CLabel,
  CSelect,
  CRow,
} from '@coreui/react'
import CIcon from '@coreui/icons-react'



import axios from 'axios';
import {getToken} from '../../../Auth/common'
import {BASE_URL} from '../../../Auth/actions'


class _form extends Component {
  constructor(props) {
    super(props);

    this.state = {
        recordStatus: "A",
        companyCode: "",
        companyName: "",
        errorMsg: false,
        isSave : false
    };
    this.submitHandler = this.submitHandler.bind(this);
    this.handleRecordStatus = this.handleRecordStatus.bind(this);
    this.handleChangeCompanyCode = this.handleChangeCompanyCode.bind(this);
    this.handleChangeCompanyName = this.handleChangeCompanyName.bind(this);    

  }

  handleRecordStatus = event => {
    this.setState({
        recordStatus: event.target.value
    });
  }

  handleChangeCompanyCode = event => {
    this.setState({
        companyCode: event.target.value
    });
  }

  handleChangeCompanyName = event => {
    this.setState({
        companyName: event.target.value
    });
  }

  serialize = obj => {
    let str = [];
    for (let p in obj)
      if (obj.hasOwnProperty(p)) {
        str.push(encodeURIComponent(p) + "=" + encodeURIComponent(obj[p]));
      }
    return str.join("&");
  };


submitHandler(e) { 
  // let token = this.getToken()            
  e.preventDefault();      
  axios(`${BASE_URL}/company/create`, {
      method: "POST",
      proxyHeaders: false,
      credentials: true,        
      headers: {              
                "x-access-token":getToken(),
                "Content-Type": "application/x-www-form-urlencoded",
                "Accept": "application/json"                  
      },
      data: this.serialize({    
          recordStatus: this.state.recordStatus,
          companyCode: this.state.companyCode,
          companyName: this.state.companyName,             
      })
  }).then(response => {
      // console.log(response)
      this.setState({isSave:true})
      this.reset()
               
  }).catch(error => {                         
      this.setState({
          errorMsg:true
      })
  });

  
              
              
}

reset(){
  this.setState({          
      recordStatus: "A",
      employeeName: "",
      password: ""   
  });
}

render() {
  
  return (
    <>
    <CRow>
        <CCol xs="12" md="12">
          <CCard>
            <CCardHeader>
              Add Company
            </CCardHeader>
            <CCardBody>
              <CForm action="" method="post" encType="multipart/form-data" className="form-horizontal">
                <CFormGroup row>
                  <CCol md="3">
                    <CLabel htmlFor="select">Record Status</CLabel>
                  </CCol>
                  <CCol xs="12" md="9">
                    <CSelect custom name="select" 
                             id="select" 
                             onChange={this.handleRecordStatus}>
                      <option value="A">Enable</option>
                      <option value="N">Disable</option>
                    </CSelect>
                  </CCol>
                </CFormGroup>

                <CFormGroup row>
                  <CCol md="3">
                    <CLabel htmlFor="text-input">Company Code</CLabel>
                  </CCol>
                  <CCol xs="12" md="9">
                    <CInput id="text-input" 
                            name="text-input"   
                            placeholder="Company Code" 
                            value={this.state.companyCode}                        
                            onChange={this.handleChangeCompanyCode}/>
                    <CFormText>ex CM0001</CFormText>
                  </CCol>
                </CFormGroup>

                <CFormGroup row>
                  <CCol md="3">
                    <CLabel htmlFor="text-input">Company Name</CLabel>
                  </CCol>
                  <CCol xs="12" md="9">
                    <CInput id="text-input" name="text-input" placeholder="Company Name" value={this.state.companyName}                        
                                onChange={this.handleChangeCompanyName}  />
                    <CFormText>Fill your company name in here</CFormText>
                  </CCol>
                </CFormGroup>
            
              </CForm>
            </CCardBody>
            <CCardFooter>
              <CButton type="submit" size="sm" color="primary"  onClick={this.submitHandler}><CIcon name="cil-scrubber" /> Submit</CButton>
            </CCardFooter>
          </CCard>         
        </CCol>        
      </CRow>
     
    </>
    )
  }
}

export default _form
